  setFormData() {
    let entities = ['city', 'plan', 'isRuralZone', 'questionaries'];
    this.databaseProvider.verifyEntities(entities)
      .then((data) => {
        if (data.length > 0 && data.filter((data) => data == false).length == 0) {
          this.setEntities();
        } else {
          this.navigateBack();
        }
      })
      .catch((error) => {
        console.error(error);
        this.restProvider.sendGoogleAnalyticsErrorData(
          'RespondentProfilePage',
          'setFormData',
          error);
        this.navigateBack();
      });
  }

  setEntities() {
    this.storage.get('city')
      .then((data) => {
        if (data != null) {
          this.city = data;
          this.cityName = this.city.name;
          this.storage.get('plan')
            .then((data) => {
              if (data != null) {
                this.plan = data;
                this.storage.get('questionaries')
                  .then((data) => {
                    this.questionaries = data;
                    if (data != null) {
                      this.storage.get('points')
                        .then((data) => {
                          if (data != null) {
                            this.points = data;
                            this.resolveLevel();
                          } else {
                            this.navigateBack();
                          }
                        })
                        .catch(() => this.navigateBack());
                    } else {
                      this.navigateBack();
                    }
                  })
                  .catch(() => this.navigateBack());
              } else {
                this.navigateBack();
              }
            })
            .catch(() => this.navigateBack());
        } else {
          this.navigateBack();
        }
      })
      .catch(() => this.navigateBack());
  }
