  saveRespondentInfo() {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    //-----------------------MANTER SEMPRE TODOS OS CAMPOS--------------------------
    let cpf = this.respondentForm.controls['cpf'];
    let email = this.respondentForm.controls['email'];
    let residenceNeighborhood = this.respondentForm.controls['residenceNeighborhood'];
    let residenceTimeRange = this.respondentForm.controls['residenceTimeRange'];
    let jobCity = this.respondentForm.controls['jobCity'];
    let jobNeighborhood = this.respondentForm.controls['jobNeighborhood'];
    let salaryRange = this.respondentForm.controls['salaryRange'];
    let age = this.respondentForm.controls['age'];
    let gender = this.respondentForm.controls['gender'];
    let name = this.respondentForm.controls['name'];
    let phone = this.respondentForm.controls['phone'];
    let code = this.respondentForm.controls['code'];
    let courseEntry = this.respondentForm.controls['courseEntry'];
    let courseLeft = this.respondentForm.controls['courseLeft'];
    let courseName = this.respondentForm.controls['courseName'];
    let residenceCity = this.respondentForm.controls['residenceCity'];
    let jobAddress = this.respondentForm.controls['jobAddress'];
    let jobName = this.respondentForm.controls['jobName'];
    let whatsapp = this.respondentForm.controls['whatsapp'];
    let residenceType = this.respondentForm.controls['residenceType'];
    let residenceMembers = this.respondentForm.controls['residenceMembers'];
    let haveChildren = this.respondentForm.controls['haveChildren'];
    let haveJob = this.respondentForm.controls['haveJob'];
    let financialAssistance = this.respondentForm.controls['financialAssistance'];
    let financialAssistanceSelected: any[] = []
    for (let i = 0; i < financialAssistance.value.length; i++) {
      if (financialAssistance.value[i] === true) {
        financialAssistanceSelected.push(this.financialAssistanceList[i]);
      }
    }
    //-----------------------MANTER SEMPRE TODOS OS CAMPOS--------------------------
    let gameType = null;
    if (this.useGame) {
      gameType = "game";
    } else {
      gameType = "not game";
    }
    if (!this.respondentForm.valid) {
      if (!cpf.valid) {
        cpf.markAsTouched();
      }
      if (!residenceNeighborhood.valid) {
        residenceNeighborhood.markAsTouched();
      }
      this.loader.dismiss();
    } else {
      //-----------------------MANTER SEMPRE TODOS OS CAMPOS--------------------------
      let respondent = new Respondent();
      respondent.cpf = cpf ? cpf.value : null;
      respondent.email = email ? email.value : null;
      respondent.residenceTimeRange = residenceTimeRange ? residenceTimeRange.value : null;
      respondent.residenceNeighborhood = residenceNeighborhood ? residenceNeighborhood.value : null;
      respondent.jobCity = jobCity ? jobCity.value : null;
      respondent.jobNeighborhood = jobNeighborhood ? jobNeighborhood.value : null;
      respondent.salaryRange = salaryRange ? salaryRange.value : null;
      respondent.ageRange = age ? age.value : null;
      respondent.gender = gender ? gender.value : null;
      respondent.name = name ? name.value : null;
      respondent.phone = phone ? phone.value : null;
      respondent.code = code ? code.value : null;
      respondent.courseEntry = courseEntry ? courseEntry.value : null;
      respondent.courseLeft = courseLeft ? courseLeft.value : null;
      respondent.courseName = courseName ? courseName.value : null;
      respondent.residenceCity = residenceCity ? residenceCity.value : null;
      respondent.jobAddress = jobAddress ? jobAddress.value : null;
      respondent.jobName = jobName ? jobName.value : null;
      respondent.whatsapp = whatsapp ? whatsapp.value : null;
      respondent.residenceType = residenceType ? residenceType.value : null;
      respondent.residenceMembers = residenceMembers ? residenceMembers.value : null;
      respondent.haveChildren = haveChildren.value;
      respondent.haveJob = haveJob.value;
      respondent.financialAssistance = financialAssistance ? JSON.stringify(financialAssistanceSelected) : null;
      respondent.type = this.userType;
      respondent.caseTest = this.caseTest;
      respondent.points = this.points;
      respondent.gameType = gameType;
      //-----------------------MANTER SEMPRE TODOS OS CAMPOS--------------------------
      //---------------------- VERIFICA SE JA EXISTE UM ALUNO COM A MATRICULA -------------------------
      this.storage.set("disciplineSelected", this.disciplineSelected);
      this.respondentProvider.getRespondentByCode(respondent.code, this.respondentCode).then((repondentCode: Respondent) => {
        if (!repondentCode) {
          //---------------------- VERIFICA SE O RESPONDENTE ESTÁ CADASTRADO -------------------------
          this.respondentProvider.getRespondentById(this.respondentId).then((result: Respondent) => {
            if (!result) {
              //------------- INSERE O RESPONDENTE  -------------
              this.respondentProvider.insertRespondent(respondent)
                .then((data: Respondent) => {
                  respondent.id = data.id;
                  this.navigate(respondent);
                }).catch((error) => {
                  console.error(error);
                  this.restProvider.sendGoogleAnalyticsErrorData(
                    'RespondentProfilePage',
                    'saveRespondentInfo',
                    error);
                  this.tryAgainSaveRespondent();
                });
            } else {
              //------------- ATUALIZA O RESPONDENTE -------------
              respondent.id = result.id;
              respondent.type = result.type;
              this.respondentProvider.updateRespondent(respondent)
                .then(() => {
                  this.navigate(respondent);
                }).catch((error) => {
                  console.error(error);
                  this.restProvider.sendGoogleAnalyticsErrorData(
                    'RespondentProfilePage',
                    'saveRespondentInfo',
                    error);
                  this.tryAgainSaveRespondent();
                });
            }
          }).catch((error) => {
            console.error(error);
            this.restProvider.sendGoogleAnalyticsErrorData(
              'RespondentProfilePage',
              'saveRespondentInfo',
              error);
            this.tryAgainSaveRespondent();
          });
        } else {
          //------------- ATUALIZA O RESPONDENTE -------------
          respondent.id = repondentCode.id;
          respondent.type = repondentCode.type;
          this.respondentProvider.updateRespondent(respondent)
            .then(() => {
              this.navigate(respondent);
            }).catch((error) => {
              console.error(error);
              this.restProvider.sendGoogleAnalyticsErrorData(
                'RespondentProfilePage',
                'saveRespondentInfo',
                error);
              this.tryAgainSaveRespondent();
            });
        }
      });
    }
  }

  tryAgainSaveRespondent() {
    let alert = this.alertCtrl.create({
      title: 'Oops!',
      message: 'Não foi possível salvar os seus dados. Por favor, tente novamente.',
      buttons: [{
        text: "Tentar novamente",
        handler: () => {
          this.saveRespondentInfo();
        }
      }]
    });
    this.loader.dismiss();
    alert.present();
  }
